
#all-work .info ul {
  padding: 0;
  clear: both;
  display: table;
}
#all-work .items {
  padding-top: 20px;
}
#all-work .item {
  width: 250px;
  float: left;
  margin-right: 25px;
  margin-bottom: 25px;
  cursor: pointer;
}
#all-work .item img {
    width: 100%;
    height: 175px;
    vertical-align: bottom;
}
#all-work .item:hover .link p {
    text-decoration: underline;
}
#all-work .item .link {
    width: 100%;
    background-color: #fff;
    color: #051740;
    padding-top: 10px;
    padding-bottom: 10px;
}
#all-work .item .link p {
      display: inline-block;
      line-height: 21px;
      text-align: left;
      margin-left: 15px;
}
#all-work .item .link a {
      color: #051740;
      text-decoration: none;
      line-height: 50px;
}
#all-work .item .link a:link, #all-work .item .link a:hover, #all-work .item .link a:visited {
        color: #051740;
}



