
@charset "UTF-8";
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td,
hr,
button,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  margin: 0;
  padding: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
}
fieldset,
img {
  border: 0;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ol,
ul {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
q:before,
q:after {
  content: '';
}
abbr,
acronym {
  border: 0;
  font-variant: normal;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}
input,
textarea,
select {
  *font-size: 100%;
}
#app {
  min-width: 1440px;
  font-size: 14px;
  font-family: 'Microsoft YaHei', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
#app .slide,
  #app .button {
    z-index: 99;
}
#app .button {
    cursor: pointer;
}
#app .first-title {
    font-size: 24px;
}
#app .second-title {
    font-size: 16px;
}
#app .blue-font {
    color: #136fe4;
}
#app .blodTitle {
    font-weight: bold;
}
.view {
  position: relative;
  height: 100%;
  margin: 0 auto;
  z-index: 98;
  color: #fff;
}
.info.scroll {
  height: 680px;
  margin-top: 90px;
  overflow-y: scroll;
  overflow-x: hidden;
}
.nav-info {
  width: 655px;
}

/* 设置滚动条的样式 */
.scroll::-webkit-scrollbar {
  width: 6px;
}

/* 滚动槽 */
.scroll::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(85, 161, 255, 0);
  border-radius: 15px;
}

/* 滚动条滑块 */
.scroll::-webkit-scrollbar-thumb {
  background: rgba(85, 161, 255, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(85, 161, 255, 0.3);
  border-radius: 15px;
}
.login {
  position: fixed;
  top: 82px;
  font-size: 14px;
  font-weight: bold;
  width: 300px;
  right: 50px;
  z-index: 99;
  color: #fff;
  text-align: right;
}
.goto.changed {
  opacity: 1;
}
.action {
  cursor: pointer;
}



