
#rule .info {
  width: 990px;
}
#rule .info .info-title.first {
    margin-top: 0;
}
#rule .info .info-title,
  #rule .info .info-main {
    width: 735px;
}
#rule .info .info-title {
    margin-top: 30px;
}
#rule .info .info-main {
    margin-top: 10px;
}
#rule .info .info-main li {
      position: relative;
      margin-top: 5px;
      padding-left: 35px;
}
#rule .info .info-main li label {
        position: absolute;
        left: 0;
}
#rule .info .info-main p .color {
      display: inline-block;
      color: #136fe4;
      margin-top: 15px;
}
#rule .info .info-main p.title {
      margin-top: 20px;
}
#rule .info .info-main p.mt {
      margin-top: 10px;
}
.indentation {
  padding-left: 35px;
}



