
#submit {
  width: 690px;
  height: 480px;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -240px;
  margin-left: -345px;
  z-index: 999;
}
#submit img {
    width: 100%;
    height: 100%;
}
#submit .submit-mask {
    background-color: #000;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0.75;
}
#submit .close {
    width: 40px;
    height: 40px;
    background-color: #0D1A46;
    position: absolute;
    top: -45px;
    right: -45px;
}
#submit .close:before {
      content: "";
      display: block;
      position: absolute;
      width: 80%;
      height: 2px;
      background-color: #fff;
      -webkit-transform: rotateZ(45deg);
              transform: rotateZ(45deg);
      top: 19px;
      left: 4px;
}
#submit .close:after {
      content: "";
      display: block;
      position: absolute;
      width: 80%;
      height: 2px;
      background-color: #fff;
      -webkit-transform: rotateZ(-45deg);
              transform: rotateZ(-45deg);
      top: 19px;
      left: 4px;
}



