
#qrcode {
  position: fixed;
  bottom: 180px;
  right: 50px;
  z-index: 99;
  clear: both;
  display: table;
  font-size: 14px;
  clear: both;
  display: table;
}
#qrcode .tab {
    font-weight: bold;
    width: 100%;
    display: table;
}
#qrcode .tab a {
      border-right: 1px solid #136fe4;
      display: table-cell;
      width: 50%;
      text-align: center;
      text-decoration: none;
      line-height: 40px;
      border-bottom: 1px solid #136fe4;
      color: #136fe4;
      background-color: #eee;
}
#qrcode .tab a.selected {
        background-color: #fff;
        font-weight: bold;
        border-bottom: none;
}
#qrcode .tab a:last-child {
        border-right: none;
}
#qrcode .content {
    padding: 20px 10px;
}
#qrcode .qrcode-code {
    float: left;
}
#qrcode .qr-info {
    float: left;
    margin-left: 10px;
    width: 140px;
    color: #136fe4;
    text-align: justify;
}
#qrcode .hoverBtn {
    opacity: 0.5;
    transition: all .3s;
}
#qrcode .hoverBtn img {
      width: 40px;
      height: 40px;
}
#qrcode .hoverBtn .qr-thum {
      padding: 10px;
      height: 40px;
      background-color: #fff;
      text-align: center;
}
#qrcode:hover {
    width: 380px;
}
#qrcode:hover .qrcode {
      display: inline-block;
}
#qrcode:hover .hoverBtn {
      opacity: 1;
}
#qrcode .hoverBtn {
    float: right;
    cursor: pointer;
}
#qrcode .qrcode {
    position: absolute;
    top: 0;
    right: 100px;
    display: none;
    width: 280px;
    background-color: #fff;
}
#qrcode .btnInfo {
    margin-top: 5px;
    width: 90px;
    height: 24px;
    line-height: 24px;
    text-align: center;
    background-color: #fff;
    color: #136fe4;
}



