
.timeline {
  position: relative;
  margin-top: 70px;
  width: 900px;
  height: 70px;
  background: url(../../../ossweb-img/image/timeline-2.png) no-repeat;
  background-position-x: 70px;
  background-position-y: 25px;
}
.timeline .line {
    margin-left: 40px;
}
.timeline ul {
    width: 100%;
    clear: both;
    display: table;
}
.timeline ul li {
      float: left;
      position: absolute;
}
.timeline ul li .point {
        position: absolute;
        width: 30px;
        height: 30px;
}
.timeline ul li .info {
        position: relative;
        width: 185px;
}
.timeline ul li.time-1 {
        top: 40px;
        left: 0;
}
.timeline ul li.time-1 .point {
          left: 20px;
}
.timeline ul li.time-1 .info {
          top: -50px;
}
.timeline ul li.time-2 {
        top: 10px;
        left: 218px;
}
.timeline ul li.time-2 .point {
          left: 0px;
}
.timeline ul li.time-2 .info {
          top: 40px;
}
.timeline ul li.time-3 {
        top: 30px;
        left: 385px;
}
.timeline ul li.time-3 .info {
          top: -65px;
          left: 15px;
}
.timeline ul li.time-4 {
        top: 20px;
        left: 580px;
}
.timeline ul li.time-4 .point {
          top: 10px;
}
.timeline ul li.time-4 .info {
          left: 10px;
          top: 45px;
}
.timeline ul li.time-5 {
        top: 5px;
        left: 750px;
}
.timeline ul li.time-5 .point {
          left: 0;
}
.timeline ul li.time-5 .info {
          top: -50px;
          left: 10px;
}
.timeline ul li p {
        padding: 0;
        line-height: 18px;
}
.tab-content {
  margin-top: 65px;
  width: 990px;
}
.tab-content .tab span {
    line-height: 40px;
    display: inline-block;
    width: 170px;
    color: #136fe4;
    cursor: pointer;
    text-align: center;
    background-color: #0e3b73;
}
.tab-content .tab span.selected {
      background-color: #136fe4;
      color: #fff;
}
.tab-content .info {
    margin-top: 20px;
}
.tab-content .info span {
      font-size: 14px;
}
.tab-content .info p {
      margin: 5px 0;
}
.tab-content .info .info-1 p {
      width: 640px;
      margin-top: 10px;
      text-align: justify;
}
.tab-content .info .left {
      width: 650px;
}
.tab-content .info .right {
      width: 650px;
      margin-top: 30px;
}
.tab-content .info ul {
      padding: 0;
      margin: 0;
}
.tab-content .info ul li {
        position: relative;
        margin: 5px 0;
}
.tab-content .info ul li span {
          position: absolute;
          left: 0;
          top: 2px;
          display: inline-block;
          width: 17px;
          height: 17px;
          margin-right: 10px;
          background-color: #136fe4;
          text-align: center;
          border-radius: 50%;
}
.tab-content .down-tempalte {
    margin-top: 50px;
}
.tab-content .download {
    margin-top: 10px;
    display: block;
    width: 200px;
    height: 60px;
    line-height: 60px;
    background: #2c8aff url(../../../ossweb-img/image/download.png) no-repeat;
    background-position-x: 43px;
    background-position-y: 18px;
    color: #fff;
    text-align: center;
    text-decoration: none;
    padding-left: 20px;
}



