.push-up-enter {
  opacity: 0;
  -webkit-transform: translate(0, 100%);
          transform: translate(0, 100%);
  transition: all .1s ease;
}
.push-up-enter-active {
  opacity: 1;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  transition: all 0.5s ease;
}
.push-up-leave {
  opacity: 1;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  transition: all .1s ease;
}
.push-up-leave-active {
  opacity: 0;
  -webkit-transform: translate(0, -100%);
          transform: translate(0, -100%);
  transition: all 0.5s ease;
}
.push-left-enter {
