          transform: translate(100%, 0);
  transition: all 1s ease;
}
.fade-enter-active {
  transition: opacity .2s ease;
}
.fade-enter,
.fade-leave-active {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
