
.trophy {
  width: 100%;
  clear: both;
  display: inline-block;
  overflow-y: scroll;
}
.trophy .title {
    margin-top: 30px;
}
.trophy .title:first-child {
      margin: 0;
}
.trophy ul.other li {
    text-align: left;
    padding: 3px 0;
    clear: both;
    display: table;
}
.trophy ul {
    clear: both;
    display: table;
    margin-top: 15px;
    padding: 0;
}
.trophy ul li {
      float: left;
      text-align: center;
}
.trophy ul li .item-inner {
        float: left;
        text-align: center;
}
.trophy ul li .image {
        width: 100%;
        height: 60px;
        display: table;
        vertical-align: bottom;
}
.trophy ul li .image img {
          vertical-align: bottom;
}
.trophy ul li p {
        margin: 5px;
}
.trophy ul.comprehensive {
    width: 100%;
}
.trophy ul.comprehensive li {
      width: 25%;
}
.trophy ul.single {
    width: 80%;
}
.trophy ul.single li {
      width: 33%;
}
.trophy ul.other {
    width: 735px;
}
.trophy .ranking {
    margin-top: 5px;
}
.trophy .gold .color {
    color: #e3c048;
}
.trophy .light .color {
    color: #888;
}
.trophy .copper .color {
    color: #d4b46f;
}
.trophy .good {
    padding-top: 8px;
}
.trophy .good .color {
      color: #448ce4;
}
.trophy .info {
    line-height: 24px;
}
.trophy .info img {
      position: relative;
      top: 3px;
      margin-right: 3px;
}



