
#review .review-items {
  width: 990px;
  height: 590px;
  overflow-y: scroll;
}
.review-items ul {
  width: 795px;
  clear: both;
}
.review-items ul li {
    float: left;
    width: 362px;
    height: 252px;
    background: url("../../../ossweb-img/image/review-item-bg.png") no-repeat;
    background-size: 360px 230px;
    background-position: 2px 22px;
    margin-right: 35px;
    margin-top: 40px;
    cursor: pointer;
}
.review-items ul li:hover .review-name {
      text-decoration: underline;
}
.review-items .review-main {
  cursor: pointer;
  position: relative;
  width: 332px;
  height: 212px;
  border: 4px solid #fff;
  overflow: hidden;
  background-color: #000;
}
.review-items .review-name {
  width: 344px;
  text-align: center;
  margin-top: 2px;
}
.pop-close {
  display: block;
  position: absolute;
  top: 0;
  right: 0px;
  font-size: 50px;
  color: white;
  line-height: 25px;
  width: 34px;
  height: 34px;
  text-align: center;
  transition: all 0.2s;
}



