
#activity-items .info ul {
  padding: 0;
  clear: both;
  display: table;
}
#activity-items .items {
  padding-top: 20px;
}
#activity-items .item {
  width: 250px;
  float: left;
  margin-right: 25px;
  margin-bottom: 25px;
  cursor: pointer;
}
#activity-items .item:hover .link a {
    text-decoration: underline;
}
#activity-items .item img {
    width: 100%;
    height: 175px;
    vertical-align: bottom;
}
#activity-items .item .link {
    width: 100%;
    height: 50px;
    background-color: #fff;
    color: #051740;
}
#activity-items .item .link a {
      color: #051740;
      text-decoration: none;
      line-height: 50px;
}
#activity-items .item .link a:link, #activity-items .item .link a:hover, #activity-items .item .link a:visited {
        color: #051740;
}



