
#mentor {
  position: relative;
}
#mentor .nav-info {
    width: 810px;
}
#mentor .title .title-info {
    width: 655px;
}
#mentor .swiper-slide {
    margin-top: 30px;
}
#mentor .info {
    position: relative;
}
#mentor .info ul {
      width: 700px;
      padding-left: 72px;
      clear: both;
      display: inline-block;
}
#mentor .info li {
      text-align: center;
      float: left;
      margin-right: 30px;
      margin-bottom: 20px;
      overflow: hidden;
}
#mentor .info li:first-child {
        margin-left: 0;
}
#mentor .info li .image {
        border: 4px solid #136fe4;
        width: 195px;
        height: 240px;
        overflow: hidden;
}
#mentor .info li img {
        width: 100%;
        height: auto;
}
#mentor .info li .info {
        margin-top: 0;
}
#mentor .person-info {
    width: 203px;
    height: 50px;
    color: #fff;
    background-color: #2471dd;
    font-weight: bold;
}
#mentor .swiper-button-prev,
  #mentor .swiper-button-next {
    position: absolute;
    top: 50%;
    margin-top: -23px;
    cursor: pointer;
}
#mentor .swiper-button-next {
    right: 184px;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
#mentor .swiper-button-prev,
  #mentor .swiper-button-next {
    background: none;
}
.swiper-content {
  position: relative;
}
.swiper-element {
  position: absolute;
  top: 0;
}



