
.get,
.submit {
  cursor: pointer;
}
.wrap-upload .info {
  margin-top: 90px;
  width: 613px;
  padding-right: 30px;
}
.wrap-upload .info .nav-info {
    width: 600px;
}
.wrap-upload .info form {
    margin-top: 20px;
}
.wrap-upload .info form .form-line {
      width: 100%;
      margin-top: 10px;
      clear: both;
      display: table;
}
.wrap-upload .info form .form-line.submit label {
        background: none;
}
.wrap-upload .info form input[type="text"],
    .wrap-upload .info form textarea {
      background-color: #d7e2ff;
}
.wrap-upload .info form label {
      float: left;
      display: inline-block;
      width: 110px;
      height: 38px;
      text-align: center;
      line-height: 38px;
      color: #fff;
      background-color: #0e3b73;
      vertical-align: top;
}
.wrap-upload .info form input[type="text"] {
      float: left;
      width: 481px;
      height: 38px;
      padding-top: 0;
      padding-bottom: 0;
      border: none;
      padding-left: 10px;
      background-color: #d7e2ff;
      color: #041540;
}
.wrap-upload .info form textarea {
      border: none;
      width: 480px;
      padding-left: 10px;
      padding-top: 10px;
      padding-bottom: 10px;
      background-color: #d7e2ff;
      color: #041540;
      font-size: 18px;
}
.wrap-upload .info form input.verification {
      width: 310px;
}
.wrap-upload .info form input.get {
      width: 135px;
      height: 38px;
      color: #fff;
      background-color: #136fe4;
      border: none;
      float: left;
      margin-left: 36px;
}
.wrap-upload .info form input.next,
    .wrap-upload .info form input.submit {
      cursor: pointer;
      width: 140px;
      height: 50px;
      background-color: #136fe4;
      color: #fff;
      border: none;
      float: left;
}
.download {
  display: block;
  width: 220px;
  height: 50px;
  line-height: 50px;
  background: #2471dd;
  color: #fff;
  text-align: center;
  text-decoration: none;
  float: left;
  margin-left: 40px;
}



