
#slide {
  position: absolute;
  top: 40px;
  transition: all 0.3s;
}
#slide ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    list-style: none;
    text-transform: uppercase;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    box-sizing: border-box;
    margin: 0;
    width: 135px;
    position: absolute;
    padding-left: 30px;
}
#slide ul li {
      cursor: pointer;
      position: relative;
      text-align: left;
      height: 54px;
      line-height: 54px;
      font-size: 18px;
      color: #fff;
}
#slide ul li:hover a {
        color: #136fe4;
}
#slide ul li a {
        color: #fff;
        text-decoration: none;
}
#slide ul li:first-child:before, #slide ul li:last-child:before {
        content: '';
        display: inline-block;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        position: absolute;
}
#slide ul li:last-child:before {
        background-color: #3250d4;
        left: -33px;
        top: 24px;
}
#slide ul li:first-child {
        margin-top: -21px;
}
#slide ul li:first-child:before {
          background-color: #2a9cef;
          left: -33px;
          top: 21px;
}
#slide ul li.selected a {
      color: #136fe4;
      font-weight: bold;
}
#slide .line {
    width: 8px;
    height: 492px;
    background: url(../../../ossweb-img/image/slide-line.png) no-repeat;
    background-position: 0px 10px;
    position: absolute;
    top: -5px;
    left: 0;
}
#slide .point {
    display: inline-block;
    position: absolute;
    width: 15px;
    height: 15px;
    left: -7px;
    top: -1000px;
    background: url(../../../ossweb-img/image/slide-point.png) no-repeat;
    transition: .5s ease;
    z-index: 999;
}
#slide .point.index-0 {
      top: -1000px;
}
#slide .point.index-1 {
      top: 51px;
}
#slide .point.index-2 {
      top: 105px;
}
#slide .point.index-3 {
      top: 159px;
}
#slide .point.index-4 {
      top: 213px;
}
#slide .point.index-5 {
      top: 267px;
}
#slide .point.index-6 {
      top: 321px;
}
#slide .point.index-7 {
      top: 375px;
}
#slide .point.index-8 {
      top: 429px;
}
#slide.pushdown {
  top: 102px;
}



