
#home {
  position: relative;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#home .bg-front,
  #home .bg-nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
#home .logo {
    position: absolute;
    left: 0;
    top: 30px;
    left: -190px;
    z-index: 999;
}
#home .bg-nav {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
#home .plane {
    width: 3%;
    position: fixed;
    top: 10%;
    left: 30%;
    -webkit-animation: leftRight 15s ease-in-out infinite;
            animation: leftRight 15s ease-in-out infinite;
}
#home .console-boat {
    width: 19%;
    position: fixed;
    bottom: -2%;
    right: 38%;
    -webkit-animation: updown 15s ease-in-out infinite;
            animation: updown 15s ease-in-out infinite;
    z-index: 998;
}
#home .headset {
    width: 7%;
    position: fixed;
    bottom: 20%;
    right: 38%;
    -webkit-animation: updown 12s ease-in-out infinite;
            animation: updown 12s ease-in-out infinite;
    z-index: 997;
}
#home .riflemen {
    width: 10%;
    position: fixed;
    bottom: -10%;
    left: 30%;
    -webkit-animation: updown 10s ease-in-out infinite;
            animation: updown 10s ease-in-out infinite;
    z-index: 994;
}
#home .tablets {
    width: 34%;
    position: fixed;
    bottom: -2%;
    right: 20%;
    -webkit-animation: updown 27s ease-in-out infinite;
            animation: updown 27s ease-in-out infinite;
