}
#home .robot-2 {
    width: 7%;
    position: fixed;
    bottom: 14%;
    right: 25%;
    -webkit-animation: updown 27s ease-in-out infinite;
            animation: updown 27s ease-in-out infinite;
    z-index: 993;
}
#home .keyboard {
    width: 10%;
    position: fixed;
    bottom: -5%;
    left: 0;
    -webkit-animation: updown 6s ease-in-out infinite;
            animation: updown 6s ease-in-out infinite;
    z-index: 995;
}
#home .planet {
    width: 13%;
    position: fixed;
    bottom: 34%;
    right: 13%;
    -webkit-animation: planet 25s ease-in-out infinite;
            animation: planet 25s ease-in-out infinite;
    z-index: 999;
    transition: all .5s;
}
#home .robot {
    width: 15%;
    position: fixed;
    bottom: 3%;
    left: 8%;
    -webkit-animation: updown 23s ease-in-out infinite;
            animation: updown 23s ease-in-out infinite;
    z-index: 996;
    transition: all .5s;
}



